import React from "react";
import Open from "../assets/open-outline.svg";
import "../css/card-socials.css";


function SocialComponent(props) {
    const items = props.items

    return (
        <div className="card--detail">
            <div className="detail--social">
                <img
                    src={items.logoUrl}
                    alt={items.title}
                    className="card--logo"
                />
                <div className="card--link">
                    <a href={items.url} target="_blank" rel="noreferrer">
                        <p>{items.title}</p>
                        <img
                            src={Open}
                            alt="Open in a new tab"
                            className="card--open"
                        />
                    </a>
                </div>
            </div>
            <img
                src={items.qrcodeUrl}
                alt="QR code"
                className="card--qrcode"
            />
        </div>
    );
}

export default SocialComponent;