import React from "react";
import SocialComponent from "../components/SocialComponent.js";
import RawData from "../Data.js";
import "../css/card-socials.css";
import Paws from "../assets/paws.svg";
import Profile from "../assets/profile-picture.svg";

const ProcessedData = RawData.map(items => {
    return (<SocialComponent
        key={items.id}
        items={items}
    />);
})

function CardSocials() {
    return (<div className="card">
            <span className="background-glass">
                <div className="background">
                    <section className="card--name">
                        <img src={Profile} alt="RazorTheWolf's Profile"/>
                        <div></div>
                        <div>
                            <h1>RazorTheWolf <img src={Paws} alt="Paws Emoji" className="paws"/></h1>
                            <h1>IT Student</h1>
                        </div>
                    </section>
                    <section className="card--socials">
                        {ProcessedData}
                    </section>
                </div>
            </span>
    </div>);
}

export default CardSocials;